
html,
body {
  height: 100%;
}
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-width: 320px;
  background: #fcfcfc;
}
p,
blockquote,
h1,
h2,
h3,
h4 {
  margin: 1em 0;
}
.page {
  overflow: hidden;
}
@font-face {
  font-family: AdineuePro;
  font-weight: 900;
  font-style: normal;
  src: url("./assets/font/adineueprokz-black.woff2") format("woff2"), url("./assets/font/adineueprokz-black.woff") format("woff"), url("./assets/font/adineueprokz-black.ttf") format("truetype");
}
@font-face {
  font-family: AdineuePro;
  font-weight: 700;
  font-style: normal;
  src: url("./assets/font/adineueprokz-bold.woff2") format("woff2"), url("./assets/font/adineueprokz-bold.woff") format("woff"), url("./assets/font/adineueprokz-bold.ttf") format("truetype");
}
@font-face {
  font-family: Lato;
  font-weight: normal;
  font-style: normal;
  src: url("./assets/font/lato-regular.woff2") format("woff2"), url("./assets/font/lato-regular.woff") format("woff"), url("./assets/font/lato-regular.ttf") format("truetype");
}
@font-face {
  font-family: Lato;
  font-weight: normal;
  font-style: italic;
  src: url("./assets/font/lato-italic.woff2") format("woff2"), url("./assets/font/lato-italic.woff") format("woff"), url("./assets/font/lato-italic.ttf") format("truetype");
}
@font-face {
  font-family: Lato;
  font-weight: 700;
  font-style: normal;
  src: url("./assets/font/lato-bold.woff2") format("woff2"), url("./assets/font/lato-bold.woff") format("woff"), url("./assets/font/lato-bold.ttf") format("truetype");
}
@font-face {
  font-family: Lato;
  font-weight: 700;
  font-style: italic;
  src: url("./assets/font/lato-bolditalic.woff2") format("woff2"), url("./assets/font/lato-bolditalic.woff") format("woff"), url("./assets/font/lato-bolditalic.ttf") format("truetype");
}
@font-face {
  font-family: Lato;
  font-weight: 100;
  font-style: normal;
  src: url("./assets/font/lato-light.woff2") format("woff2"), url("./assets/font/lato-light.woff") format("woff"), url("./assets/font/lato-light.ttf") format("truetype");
}
@font-face {
  font-family: Lato;
  font-weight: 100;
  font-style: italic;
  src: url("./assets/font/lato-lightitalic.woff2") format("woff2"), url("./assets/font/lato-lightitalic.woff") format("woff"), url("./assets/font/lato-lightitalic.ttf") format("truetype");
}
button {
  display: inline-block;
  cursor: pointer;
  outline: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -ms-touch-action: manipulation;
      touch-action: manipulation;
}
body {
  font-family: Lato, Arial, sans-serif;
  font-size: 17px;
  letter-spacing: 0.34px;
  line-height: 1.53;
  color: #161616;
}
a {
  text-decoration: none;
  position: relative;
  color: #1000fb;
  border-bottom: solid #cfcbff 1px;
  transition: color 0.3s, border 0.2s;
}
a:hover {
  border-bottom: solid #6e64ff 1px;
}
a.pseudo {
  color: inherit;
  border-bottom: dashed #c5c5c5 1px;
}
a.pseudo:visited {
  color: inherit;
  border-bottom: solid #e2e2e2 1px;
}
a.pseudo:hover,
a.pseudo:visited:hover {
  color: inherit;
  border-bottom: dashed #8a8a8a 1px;
}
.link a {
  color: inherit;
  border-bottom: solid transparent 1px;
}
.link a:hover {
  border-bottom: solid #c5c5c5 1px;
}
a:visited {
  color: #6d6d6d;
  border-bottom: solid #e2e2e2 1px;
}
a:visited:hover {
  color: #4c4c4c;
  border-bottom: solid #c5c5c5 1px;
}
a.more {
  border-bottom: none;
}
a.more:hover {
  border-bottom: none;
}
h1,
.h1,
h2,
.h2,
h3,
.h3,
.title {
  font-family: AdineuePro, Arial, sans-serif;
  text-transform: uppercase;
  color: #1d1d1d;
}
h1,
.h1 {
  font-size: 36px;
  letter-spacing: 2.2px;
  line-height: 1.33;
}
h3,
.h3 {
  font-size: 15px;
  letter-spacing: 1.2px;
  line-height: 1.73;
}
.title {
  font-size: 36px;
  letter-spacing: 20px;
  line-height: 2.08;
  margin: 40px 0;
}
h1.title {
  margin: 60px 0;
}
blockquote {
  font-style: italic;
}
blockquote::before,
blockquote::after {
  content: '';
  z-index: -1;
  background: url("~@/assets/svg-for-css/quote.svg");
  background-repeat: repeat-x;
  background-size: 37px 40px;
  opacity: 0.1;
  pointer-events: none;
  display: inline;
  position: absolute;
  -webkit-transform: translate(-30px, -8px);
          transform: translate(-30px, -8px);
  width: 74px;
  height: 40px;
}
@media print {
blockquote::before,
  blockquote::after {
    content: '';
    display: none;
}
}
blockquote::after {
  -webkit-transform: scale(-1, 1) translate(52px, -8px);
          transform: scale(-1, 1) translate(52px, -8px);
}
small {
  font-size: 15px;
}
small.extra {
  font-size: 10px;
}
p[data-first-letter] {
  position: relative;
}
p[data-first-letter]::before {
  content: attr(data-first-letter);
  pointer-events: none;
  position: absolute;
  top: -16px;
  left: -4px;
  font-size: 54px;
  line-height: 1;
  color: #1d1d1d;
  opacity: 0.1;
}
p.accent {
  margin: 45px 0;
  padding-left: 20px;
  border-left: solid 1px #6d6d6d;
}
button {
  font-size: 13px;
  border: none;
  box-sizing: content-box;
  padding: 20px;
  min-width: 160px;
  line-height: 1.07;
  transition: background-color 0.2s, color 0.4s;
}
button.accent {
  font-size: 15px;
  background-color: #ff3b3b;
  margin: 10px;
  color: #fff;
  border-radius: 30px;
}
button.accent:hover {
  background-color: #db3131;
}
button.accent[disabled]:hover {
  background-color: #d2d8da;
}
button[disabled] {
  cursor: not-allowed;
  background-color: #d2d8da;
}
.label,
.menu-link {
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 1.2px;
}
.more {
  font-size: 10px;
  line-height: 2.6;
  letter-spacing: 0.3px;
  text-transform: uppercase;
  transition: color 0.3s, opacity 0.3s;
  color: #6d6d6d;
  padding: 5px 5px 5px 0;
}
.more::after {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  content: '';
  white-space: nowrap;
  background: url("~@/assets/svg-for-css/arrow-more.svg?fill=%236d6d6d");
  width: 18px;
  height: 10px;
  display: inline-block;
  vertical-align: initial;
  margin-left: 8px;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}
.more:visited:hover,
.more:hover {
  color: #000;
}
.more:visited:hover::after,
.more:hover::after {
  -webkit-transform: translateX(5px);
          transform: translateX(5px);
}
.more.action {
  color: #f00;
  font-size: 11px;
  font-weight: 900;
  opacity: 0.6;
}
.more.action::after {
  background: url("~@/assets/svg-for-css/arrow-more.svg?fill=%23f00");
  opacity: 0.6;
}
.more.action:hover {
  color: #f00;
  opacity: 1;
}
body::before {
  content: '';
  display: block;
  height: 0px;
  height: 100px;
}
body.mobile-menu-active {
  overflow: hidden;
}


