
.slider {
  display: block;
  max-width: 545px;
  position: relative;
}
.slider::after {
  content: '';
  display: block;
  padding-bottom: 100%;
  margin-bottom: 45px;
}
.slide1,
.slide2,
.slide3,
.slide4 {
  position: absolute;
  border-radius: 10px;
  box-shadow: 0 6px 12px rgba(20,13,2,0.53);
  margin: 0;
  font-size: 0;
}
.slide1 img,
.slide2 img,
.slide3 img,
.slide4 img {
  border-radius: 10px;
  width: 100%;
}
.slide1 {
  top: 0;
  z-index: 4;
}
.slide2 {
  z-index: 3;
  top: 30px;
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}
.slide3 {
  z-index: 2;
  top: 60px;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}
.slide4 {
  opacity: 0;
  z-index: 1;
  top: -90px;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.slider.next-slide .slide1,
.slider.next-slide .slide2,
.slider.next-slide .slide3,
.slider.next-slide .slide4 {
  transition: opacity 0.4s, top 0.4s, -webkit-transform 0.4s;
  transition: opacity 0.4s, transform 0.4s, top 0.4s;
  transition: opacity 0.4s, transform 0.4s, top 0.4s, -webkit-transform 0.4s;
}
.slider.next-slide .slide1 {
  top: -90px;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  opacity: 0;
}
.slider.next-slide .slide2 {
  top: 0px;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.slider.next-slide .slide3 {
  top: 30px;
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}
.slider.next-slide .slide4 {
  transition: opacity 0.4s, top 0.4s, -webkit-transform 0.1s;
  transition: opacity 0.4s, transform 0.1s, top 0.4s;
  transition: opacity 0.4s, transform 0.1s, top 0.4s, -webkit-transform 0.1s;
  opacity: 1;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  top: 60px;
}
.slider.prev-slide .slide1,
.slider.prev-slide .slide2,
.slider.prev-slide .slide3,
.slider.prev-slide .slide4 {
  transition: opacity 0.4s, top 0.4s, -webkit-transform 0.4s;
  transition: opacity 0.4s, transform 0.4s, top 0.4s;
  transition: opacity 0.4s, transform 0.4s, top 0.4s, -webkit-transform 0.4s;
}
.slider.prev-slide .slide1 {
  top: 30px;
  z-index: 3;
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}
.slider.prev-slide .slide2 {
  top: 60px;
  z-index: 2;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}
.slider.prev-slide .slide3 {
  opacity: 0;
  z-index: 1;
  top: 60px;
  -webkit-transform: scale(0.85);
          transform: scale(0.85);
}
.slider.prev-slide .slide4 {
  top: 0;
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
  z-index: 4;
}
.slider .controls {
  position: relative;
  z-index: 5;
}


